@use '../base/variables' as v;
@use '../base/mixins' as m;

.no-proyectos{
    a{
        color:v.$indigo;
    }
}

.listado-proyectos{
    padding:0;
    list-style: none;
    margin-top: 3rem;
    @include m.grid(1,2rem);
    @include m.tablet {
        @include m.grid(2,2rem);
    }
    @include m.desktop {
        @include m.grid(3,2rem);
    }

    .proyecto{
        line-height: 2;
        background-color: lighten($color: v.$indigo, $amount: 5%);
        min-height: 12rem;
        display: flex;
        align-items: center;
        padding: 2rem;
        border-radius: .5rem;

        &:hover{
            background-color: darken(v.$indigo,5%);
        }

        a{
            color:v.$blanco;
        }
    }
}