@use '../base/mixins' as m;
@use '../base/variables' as v;

.paginacion{
    margin-top: 5rem;
    margin-bottom: 3rem;
    display: flex;
    justify-content: center;
    gap: 1rem;
    flex-wrap: wrap;
    @include m.tablet {
        justify-content:flex-end;
    }

    &__enlace{
        color:v.$azul;
        font-size: 1.6rem;
        &--texto{
            padding: 1rem;
            border-radius: .5rem;
            border: 1px solid v.$azul;

            &:hover	{
                color:v.$blanco;
                background-color: v.$azul;
            }

        }

        &--numero{
            font-weight: bold;
            display:none;
            @include m.tablet {
                display:inline;
            }
        }

        &--actual{
            color:v.$gris;
            font-weight: bold;
            display:none;
            @include m.tablet {
                display:inline;
            }
        }
    }
}