/** Media Queries **/
body {
  background-color: #F8FAFC;
}

.login__tagline {
  font-family: "Inter", sans-serif;
  font-weight: 900;
  color: #000;
  font-size: 3rem;
  text-align: center;
}
.login__contenedor {
  width: min(100%, 50rem);
  margin: 0 auto;
  background-color: rgba(0, 0, 0, 0.6);
  padding: 3rem 5rem;
  border-radius: 2rem;
  margin-bottom: 2rem;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 13px 27px -5px, rgba(0, 0, 0, 0.3) 0px 8px 16px -8px;
}
.login__titulo {
  background: linear-gradient(to right, #0891B2 0%, #5bd9f8 100%);
  color: transparent;
  background-clip: text;
  text-align: center;
  font-size: 7rem;
  margin-top: 2rem;
  font-size: 4.5rem;
}
@media (min-width: 768px) {
  .login__titulo {
    font-size: 10rem;
  }
}
.login__descripcion {
  color: #0891B2;
  font-weight: bold;
  font-size: 2rem;
  margin-bottom: 3rem;
  text-transform: uppercase;
  text-align: center;
}
@media (min-width: 768px) {
  .login__descripcion {
    font-size: 2rem;
  }
}

.footer {
  margin: 0;
  font-size: 2.2rem;
}
.footer--regular {
  font-size: 1.5rem;
}

