*,
*:before,
*:after {
  box-sizing: inherit;
}

html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  font-family: "Open Sans", sans-serif;
  font-size: 1.6rem;
  min-height: 100%;
}
body.pausar {
  overflow-y: hidden;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

.contenedor-sm {
  max-width: 70rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

.d-block {
  display: block;
}

.flex-derecha {
  display: flex;
  justify-content: end;
}

.flex-izquierda {
  display: flex;
  justify-content: start;
  gap: 2rem;
  align-items: flex-start;
  margin-top: 2rem;
}
@media (min-width: 768px) {
  .flex-izquierda {
    margin-top: 0;
  }
}

.no-margin {
  margin: 0;
}

.mt-1 {
  margin-top: 0.5rem;
}

.mt-2 {
  margin-top: 2rem;
}

.mt-3 {
  margin-top: 3rem;
}

.mt-4 {
  margin-top: 4rem;
}

.flex-centro {
  display: flex;
  justify-content: center;
  gap: 2rem;
}

.flex-centro-col {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.btn, .btn-submit {
  border: none;
  font-size: 1.5rem;
  padding: 1rem 1.5rem;
  text-transform: uppercase;
  font-weight: 700;
  border-radius: 1rem;
}
.btn:hover, .btn-submit:hover {
  cursor: pointer;
}

.btn-paginador {
  color: #FFFFFF;
  background-color: #2563EB;
  font-size: 1.5rem;
  transition: background-color 500ms ease;
}
.btn-paginador:hover {
  cursor: pointer;
  background-color: #0e3b9c;
}
.btn-paginador:disabled {
  cursor: not-allowed;
  opacity: 0.5;
}

.btn-editar {
  color: #FFFFFF;
  background-color: #F59E0B;
}

.btn-eliminar {
  color: #FFFFFF;
  background-color: rgb(169, 0, 0);
}

.btn-submit {
  width: 100%;
  color: #FFFFFF;
  background-color: #2563EB;
  transition: background-color 500ms ease-in-out;
}
@media (min-width: 768px) {
  .btn-submit {
    width: auto;
  }
}
.btn-submit:hover {
  background-color: #134cca;
}

.btn-agregar,
.btn-regresar {
  background-color: #2563EB;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  border-radius: 0.5rem;
}
@media (min-width: 768px) {
  .btn-agregar,
.btn-regresar {
    width: auto;
  }
}
.btn-agregar:hover,
.btn-regresar:hover {
  background-color: #134cca;
  cursor: pointer;
}

.btn-verde {
  background-color: rgb(0, 120, 0);
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  border-radius: 0.5rem;
}
@media (min-width: 768px) {
  .btn-verde {
    width: auto;
  }
}
.btn-verde:hover {
  background-color: #004500;
  cursor: pointer;
}

.btn-editar {
  color: #FFFFFF;
  background-color: #2563EB;
  padding: 1.5rem 4rem;
  font-weight: bold;
  border: none;
  border-radius: 0.5rem;
}
.btn-editar:hover {
  cursor: pointer;
  background-color: #0e3b9c;
}

.contenedor-sombra {
  box-shadow: rgba(50, 50, 93, 0.25) 0px 13px 27px -5px, rgba(0, 0, 0, 0.3) 0px 8px 16px -8px;
  background-color: #FFFFFF;
  border-radius: 1rem;
  padding: 2rem;
}

.w-100 {
  width: 100%;
}

.w-auto {
  width: 100%;
}
@media (min-width: 768px) {
  .w-auto {
    width: auto;
  }
}

.resaltar-texto {
  font-weight: bold;
  font-size: 2rem;
}

h1, h2, h3 {
  font-family: "Inter", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
}

h1 {
  font-size: 6rem;
}

h2 {
  font-size: 5rem;
}

h3 {
  font-size: 4rem;
}

p {
  font-size: 2rem;
  color: #6b7280;
  line-height: 1.8;
}

.descripcion-pagina {
  text-align: center;
}

.formulario__fieldset {
  margin-top: 5rem;
  border: none;
  border-top: solid 0.1rem #6b7280;
}
.formulario__fieldset .formulario__legend {
  text-align: center;
  margin-bottom: 2rem;
  font-weight: bold;
  font-size: 2rem;
  text-transform: uppercase;
}
.formulario__descripcion {
  font-size: 1.8rem;
}
.formulario__enlace {
  display: block;
  color: #2563EB;
  margin-top: 0.2rem;
  font-size: 1.7rem;
  margin-bottom: 1rem;
}
@media (min-width: 480px) {
  .formulario__enlace {
    display: inline-block;
    margin-right: 1rem;
  }
}
.formulario__enlace--eliminar {
  color: #f60000;
}
.formulario__enlace--eliminar:hover {
  cursor: pointer;
  color: rgb(169, 0, 0);
}
.formulario__enlace:hover {
  cursor: pointer;
  color: #0c3284;
}
.formulario__contenedor {
  margin-bottom: 2rem;
}
.formulario__contenedor--radios {
  display: flex;
  justify-content: start;
  gap: 1rem;
}
.formulario__contenedor--radios .formulario__label {
  font-size: 1.5rem;
}
.formulario__contenedor .formulario__descripcion {
  font-size: 2rem;
  margin: 1rem 0;
  display: block;
}
.formulario__contenedor-campos {
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(2, 1fr);
}
@media (min-width: 1024px) {
  .formulario__contenedor-campos {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.formulario__radio {
  display: flex;
  gap: 1rem;
}
.formulario__campo {
  margin-bottom: 2rem;
}
.formulario__campo--extender {
  grid-column: 1/3;
}
@media (min-width: 768px) {
  .formulario__campo--extender {
    grid-column: unset;
  }
}
.formulario__campo .formulario__label {
  display: block;
  margin-bottom: 1rem;
  color: #FFFFFF;
  font-size: 2rem;
  font-weight: bold;
}
.formulario__campo .formulario__input {
  width: 100%;
  padding: 1rem;
  border: none;
  border-radius: 0.5rem;
}
.formulario__campo .formulario__input--select {
  width: 50%;
}
.formulario__campo .formulario__input:disabled,
.formulario__campo .formulario__input--select:disabled,
.formulario__campo p {
  opacity: 0.5;
  background-color: #cacdd3;
  color: #000;
  font-weight: bold;
  cursor: not-allowed;
}
.formulario__campo p {
  border-radius: 0.5rem;
  padding: 0.2rem 0.5rem;
  font-size: 1.8rem;
}
.formulario__campo .formulario__descripcion {
  opacity: 0.5;
  background-color: #cacdd3;
  color: #000;
  font-weight: bold;
  border-radius: 0.5rem;
  padding: 0.2rem 0.5rem;
  font-size: 1.8rem;
}
.formulario__campo .formulario__descripcion:hover, .formulario__campo .formulario__descripcion--resaltar {
  cursor: pointer;
  background-color: #2563EB;
  opacity: 1;
  color: #FFFFFF;
}

.alerta {
  padding: 1rem 1rem 1rem 2rem;
  text-transform: uppercase;
  font-weight: 900;
  margin: 1rem 0;
  font-size: 1.2rem;
  width: 100%;
}
.alerta--error {
  background-color: #ffdcdc;
  border-left: 0.5rem solid rgb(169, 0, 0);
  color: rgb(169, 0, 0);
}
.alerta--exito {
  background-color: #abffab;
  border-left: 0.5rem solid rgb(0, 120, 0);
  color: rgb(0, 120, 0);
}

.table {
  width: 100%;
  border-collapse: collapse;
}
.table__head {
  background-color: #1E293B;
  color: #FFFFFF;
}
.table__caption {
  width: 85%;
  background-color: #40587f;
  color: #FFFFFF;
  text-align: start;
  padding: 1rem;
}
.table__caption--categoria {
  border-radius: 0.5rem;
  padding: 1rem 0.5rem 0.5rem 0.5rem;
}
.table__caption--resaltar {
  background-color: #6b7280;
}
.table__caption span {
  font-size: 1.8rem;
  font-weight: bold;
}
.table__th {
  padding: 1rem;
  text-align: left;
}
.table__th--ocultar {
  display: none;
}
@media (min-width: 1024px) {
  .table__th--ocultar {
    display: inline-block;
  }
}
.table__tr:nth-child(even) {
  background-color: #e6edf4;
}
.table__td, .table__td--acciones {
  padding: 1.5rem 1rem;
}
.table__td--slp-naranja {
  background-color: #F59E0B;
}
.table__td--slp-naranja:hover {
  cursor: pointer;
}
.table__td--resaltar {
  background-color: #F59E0B;
}
.table__td--resaltar:hover {
  cursor: pointer;
}
.table__td--prolamsa:hover, .table__td--arcoMetal:hover {
  cursor: pointer;
}
.table__td--acciones {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  gap: 2rem;
}
.table__td--ocultar {
  display: none;
}
@media (min-width: 1024px) {
  .table__td--ocultar {
    display: inline-block;
  }
}
.table__accion {
  all: unset;
  font-weight: bold;
  font-size: 1.4rem;
  text-transform: uppercase;
}
.table__accion--editar {
  color: #2563EB;
}
.table__accion--eliminar {
  color: rgb(169, 0, 0);
}
.table__accion:hover {
  cursor: pointer;
}

.paginacion {
  margin-top: 5rem;
  margin-bottom: 3rem;
  display: flex;
  justify-content: center;
  gap: 1rem;
  flex-wrap: wrap;
}
@media (min-width: 768px) {
  .paginacion {
    justify-content: flex-end;
  }
}
.paginacion__enlace {
  color: #2563EB;
  font-size: 1.6rem;
}
.paginacion__enlace--texto {
  padding: 1rem;
  border-radius: 0.5rem;
  border: 1px solid #2563EB;
}
.paginacion__enlace--texto:hover {
  color: #FFFFFF;
  background-color: #2563EB;
}
.paginacion__enlace--numero {
  font-weight: bold;
  display: none;
}
@media (min-width: 768px) {
  .paginacion__enlace--numero {
    display: inline;
  }
}
.paginacion__enlace--actual {
  color: #6b7280;
  font-weight: bold;
  display: none;
}
@media (min-width: 768px) {
  .paginacion__enlace--actual {
    display: inline;
  }
}

.dashboard {
  background-color: #F8FAFC;
}
@media (min-width: 768px) {
  .dashboard {
    display: flex;
    height: 100vh;
  }
}
.dashboard .principal {
  flex: 1;
  overflow-y: scroll;
}
.dashboard .contenido {
  padding: 2rem;
}
.dashboard .contenido .nombre-pagina {
  font-size: 2.8rem;
  text-align: center;
}
.dashboard .formulario__label, .dashboard .formulario__label--radio {
  color: #6b7280;
  font-size: 2rem;
}
.dashboard .formulario__label--radio {
  font-size: 1.7rem;
}
.dashboard .formulario__input {
  background-color: #f0f2f7;
  width: 100%;
}
.dashboard .formulario__input--select {
  width: 100%;
}
.dashboard .formulario__campos-grid {
  display: grid;
  gap: 1rem;
  grid-template-columns: repeat(2, 1fr);
}
@media (min-width: 1024px) {
  .dashboard .fieldset-grid {
    display: grid;
    gap: 3rem;
    grid-template-columns: repeat(2, 1fr);
  }
}

.sidebar {
  opacity: 0;
  height: 0;
  overflow: hidden;
  transition-property: opacity;
  transition-duration: 500ms;
  background-color: #2f405d;
}
.sidebar--mostrar {
  opacity: 1;
  height: auto;
  padding: 2rem;
  position: absolute;
  z-index: 100;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.9);
}
@media (min-width: 768px) {
  .sidebar--mostrar {
    display: none;
  }
}
.sidebar--ocultar {
  opacity: 0;
}
@media (min-width: 768px) {
  .sidebar {
    opacity: 1;
    height: auto;
    background-color: #2f405d;
    padding: 2rem;
  }
}
.sidebar__contenedor {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 2rem;
}
@media (min-width: 480px) {
  .sidebar__contenedor {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    gap: 0;
  }
}
.sidebar__contenedor .sidebar__cerrar-menu {
  display: flex;
  justify-content: center;
  height: 2rem;
  width: 2rem;
}
.sidebar__contenedor .sidebar__cerrar-menu img:hover {
  cursor: pointer;
}
@media (min-width: 768px) {
  .sidebar__contenedor .sidebar__cerrar-menu {
    display: none;
  }
}
.sidebar__contenedor h2 {
  margin: 0;
  color: #FFFFFF;
  text-align: center;
}
.sidebar__nav {
  margin-top: 5rem;
}
.sidebar__nav .sidebar__enlace {
  color: #FFFFFF;
  display: block;
  margin-bottom: 2rem;
  padding: 0.7rem;
  transition-property: background-color;
  transition-duration: 300ms;
  text-transform: uppercase;
  font-weight: bold;
  font-size: 2.4rem;
  text-align: center;
}
.sidebar__nav .sidebar__enlace:hover {
  background-color: #40587f;
  border-radius: 0.5rem;
}
@media (min-width: 768px) {
